<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-table :data="rigthsList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="authName"></el-table-column>
        <el-table-column label="" prop="path"></el-table-column>
        <el-table-column label="" prop="level">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.level === '0'"></el-tag>
            <el-tag type="warning" v-else-if="scope.row.level === '1'"></el-tag>
            <el-tag type="danger" v-else="scope.row.level === '2'"></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      rigthsList: []
    }
  },
  async created () {
    let { data: res } = await this.$http.get('rights/list')
    this.rigthsList = res.data
    console.log(this.rigthsList)
  }
}
</script>
