<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input placeholder="" clearable @clear="getUserList" v-model="queryuser.query">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" @click="dialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <el-table :data="userList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="email"></el-table-column>
        <el-table-column label="" prop="mobile"></el-table-column>
        <el-table-column label="" prop="role_name"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state" @change="changeState(scope.row)"></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" class="btn" width="180">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="editUserInfo(scope.row.id)"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="delUser(scope.row.id)"></el-button>
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="getRights(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!-- -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryuser.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryuser.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="this.totals"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="50%">
      <el-form :model="addUser" :rules="rules" ref="ruleForm" label-width="70px">
        <el-form-item label="" prop="username">
          <el-input v-model="addUser.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addUser.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addUser.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addUser.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="reset"> </el-button>
        <el-button type="primary" @click="add()"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%">
      <el-form :model="editUser" :rules="editrules" ref="editForm" label-width="70px">
        <el-form-item label="" prop="username">
          <el-input v-model="editUser.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editUser.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editUser.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="editReset"> </el-button>
        <el-button type="primary" @click="editAdd"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="rightDialogVisible" width="50%" @close="closeRightDialog">
      <p>: {{ current.username }}</p>
      <p>: {{ current.role_name }}</p>
      <span> </span>
      <el-select v-model="check" placeholder="">
        <el-option v-for="item in rightsList" :key="item.id" :label="item.roleName" :value="item.id"></el-option>
      </el-select>
      <span slot="footer">
        <el-button @click="rightsReset"> </el-button>
        <el-button type="primary" @click="editrole()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    var checkEmail = (rule, value, callback) => {
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      if (regEmail.test(value)) {
        callback()
      }
      callback(new Error(''))
    }
    var checkmobile = (rule, value, callback) => {
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(value)) {
        callback()
      }
      callback(new Error(''))
    }
    return {
      queryuser: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      addUser: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      editUser: {
        id: '',
        username: '',
        email: '',
        mobile: ''
      },
      rights: {
        username: '',
        authName: ''
      },
      rightDialogVisible: false,
      totals: 0,
      userList: [],
      dialogVisible: false,
      rules: {
        username: [{ required: true, message: '', trigger: 'blur' }, { min: 3, max: 8, message: ' 3  8', trigger: 'blur' }],
        password: [{ required: true, message: '', trigger: 'blur' }, { min: 6, max: 16, message: ' 6  16 ', trigger: 'blur' }],
        email: [{ required: true, message: '', trigger: 'blur' }, { validator: checkEmail, trigger: 'blur' }],
        mobile: [{ required: true, message: '', trigger: 'blur' }, { validator: checkmobile, trigger: 'blur' }]
      },
      editrules: {
        email: [{ required: true, message: '', trigger: 'blur' }, { validator: checkEmail, trigger: 'blur' }],
        mobile: [{ required: true, message: '', trigger: 'blur' }, { validator: checkmobile, trigger: 'blur' }]
      },
      editDialogVisible: false,
      rightsList: [],
      check: '',
      current: {}
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    async getUserList() {
      let { data: res } = await this.$http.get('users', { params: this.queryuser })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.totals = res.data.total
      this.userList = res.data.users
    },
    handleSizeChange(newSize) {
      this.queryuser.pagesize = newSize
      this.getUserList()
    },
    handleCurrentChange(newNum) {
      this.queryuser.pagenum = newNum
      this.getUserList()
    },
    async changeState(userInfo) {
      // console.log(userInfo)
      let { data: res } = await this.$http.put(`users/${userInfo.id}/state/${userInfo.mg_state}`)
      if (res.meta.status !== 200) {
        userInfo.mg_state = !userInfo.mg_state
        return this.$message.error('')
      }
      this.$message.success('')
    },
    add() {
      this.$refs.ruleForm.validate(async valid => {
        console.log(valid)
        if (!valid) {
          return
        }
        let { data: res } = await this.$http.post('users', this.addUser)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        console.log(res)
        this.$message.success('')
        this.dialogVisible = false
        this.getUserList()
      })
    },
    reset() {
      this.$refs.ruleForm.resetFields()
      this.dialogVisible = false
    },
    // 
    async editUserInfo(id) {
      this.editDialogVisible = true
      let { data: res } = await this.$http.get('users/' + id)
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editUser.username = res.data.username
      this.editUser.id = res.data.id
      this.editUser.email = res.data.email
      this.editUser.mobile = res.data.mobile
    },
    // 
    editReset() {
      this.$refs.editForm.resetFields()
      this.editDialogVisible = false
    },
    // 
    editAdd() {
      this.$refs.editForm.validate(async valid => {
        console.log(valid)
        if (!valid) {
          return
        }
        let { data: res } = await this.$http.put('users/' + this.editUser.id, this.editUser)
        if (res.meta.status !== 200) {
          this.$message.error('')
        }
        this.editDialogVisible = false
        this.getUserList()
      })
      //    console.log(result)
    },
    // 
    async delUser(id) {
      console.log(id)
      let res = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      if (res !== 'confirm') {
        return this.$message.info('')
      }

      let { data: re } = await this.$http.delete('users/' + id)
      // console.log(re)
      if (re.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getUserList()
    },
    async getRights(id) {
      console.log(id)
      this.current.username = id.username
      this.current.role_name = id.role_name

      this.current.id = id.id
      //    roles/:id
      let { data: res } = await this.$http.get(`roles`)

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      console.log(res.data)
      this.rightsList = res.data
      console.log(this.rightsList)
      this.rightDialogVisible = true
      // console.log(id)
    },
    rightsReset() {
      this.rightDialogVisible = false
    },
    closeRightDialog() {
      this.check = ''
    },
    async editrole(id) {
      if (!this.check) {
        return this.$message.error('')
      }
      let { data: res } = await this.$http.put(`users/${this.current.id}/role`, {
        rid: this.check
      })
      console.log(res)
      console.log(this.current.id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.rightDialogVisible = false
      this.getUserList()
    }
  }
}
</script>
<style lang="less" scoped></style>
