<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-button type="primary" @click="addRole"></el-button>
      <el-table :data="rolesList" border stripe>
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row v-for="(item1, index1) in scope.row.children" :key="item1.id" :class="['borderBottom', index1 === 0 ? 'borderTop' : '']">
              <el-col :span="5">
                <el-tag closable @close="delRight(scope.row, item1.id)">{{ item1.authName }}</el-tag>
                <span class="el-icon-caret-right"></span>
              </el-col>
              <el-col :span="19">
                <el-row v-for="(item2, index2) in item1.children" :key="item2.id" :class="['borderTop', index2 === 0 ? 'borderTopc' : '']">
                  <el-col :span="6">
                    <el-tag type="warning" closable @close="delRight(scope.row, item2.id)">{{ item2.authName }}</el-tag>
                    <span class="el-icon-caret-right"></span>
                  </el-col>
                  <el-col :span="18">
                    <el-tag type="danger" v-for="item3 in item2.children" :key="item3.id" :class="['tag3']" closable @close="delRight(scope.row, item3.id)">{{ item3.authName }}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
           
          </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit" @click="editRole(scope.row.id)"></el-button>
            <el-button size="mini" type="danger" icon="el-icon-delete" @click="deleteRole(scope.row.id)"></el-button>
            <el-button size="mini" type="setting" icon="el-icon-setting" @click="roleRight(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="DialogVisible" width="50%">
      <el-form :model="addRoles" :rules="addrules" ref="addForm" label-width="80px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="addRoles.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addRoles.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="addReset"> </el-button>
        <el-button type="primary" @click="add"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%">
      <el-form :model="editRoles" :rules="editrules" ref="editForm" label-width="80px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="editRoles.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="editRoles.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="editReset"> </el-button>
        <el-button type="primary" @click="edit"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="rightdialogVisible" width="50%" @close="closerightdialog">
      <el-tree :data="getAllRole" :props="pro" show-checkbox default-expand-all node-key="id" :default-checked-keys="defaultCheck" ref="tree"> </el-tree>

      <span slot="footer">
        <el-button @click="rightdialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { async } from 'q'
import { constants } from 'crypto'

export default {
  data() {
    return {
      rolesList: [],
      addRoles: {
        roleDesc: '',
        roleName: ''
      },
      editRoles: {
        id: '',
        roleDesc: '',
        roleName: ''
      },
      addrules: {
        roleName: [{ required: true, message: '', trigger: 'blur' }, { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }]
      },
      editrules: {
        roleName: [{ required: true, message: '', trigger: 'blur' }, { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }]
      },
      DialogVisible: false,
      editDialogVisible: false,
      rightdialogVisible: false,
      getAllRole: '',
      pro: {
        label: 'authName',
        children: 'children'
      },
      defaultCheck: [],
      roleId: ''
    }
  },
  created() {
    this.getRoles()
  },
  methods: {
    async getRoles() {
      let { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.rolesList = res.data
      console.log(this.rolesList)
    },
    addRole() {
      this.DialogVisible = true
    },
    addReset() {
      this.$refs.addForm.resetFields()
      this.DialogVisible = false
    },
    add() {
      this.$refs.addForm.validate(async valid => {
        //  console.log(valid)
        if (!valid) return
        let { data: res } = await this.$http.post('roles', this.addRoles)
        //console.log(res)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.DialogVisible = false
        this.getRoles()
      })
    },
    async editRole(id) {
      this.editDialogVisible = true
      let { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editRoles.roleName = res.data.roleName
      this.editRoles.roleDesc = res.data.roleDesc
      this.editRole.id = id
    },
    editReset() {
      this.$refs.editForm.resetFields()
      this.editDialogVisible = false
    },
    edit() {
      this.$refs.editForm.validate(async valid => {
        if (!valid) return
        let { data: res } = await this.$http.put('roles/' + this.editRole.id, this.editRoles)
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.editDialogVisible = false
        this.getRoles()
      })
    },
    async deleteRole(id) {
      let result = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      //console.log(result)
      if (result === 'confirm') {
        let { data: res } = await this.$http.delete('roles/' + id)
        if (res.meta.status !== 200) {
          return this.$message.error('')
        } else {
          this.$message.success('')
          this.getRoles()
        }
      } else {
        return this.$message.info('')
      }
    },
    async delRight(role, rigthid) {
      let result = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (result !== 'confirm') {
        return
      }
      //console.log(rigthid)
      let { data: res } = await this.$http.delete(`roles/${role.id}/rights/${rigthid}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      role.children = res.data
    },
    async roleRight(role) {
      let { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      //
      // console.log(role.id)
      this.roleId = role.id
      this.getAllRole = res.data
      this.rightdialogVisible = true
      this.getLerRole(role, this.defaultCheck)
    },
    getLerRole(node, arr) {
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(element => {
        this.getLerRole(element, arr)
      })
    },
    closerightdialog() {
      this.defaultCheck = []
    },
    async editRights() {
      console.log(this.$refs.tree.getHalfCheckedKeys())
      console.log(this.$refs.tree.getCheckedKeys())
      let keys = [...this.$refs.tree.getCheckedKeys(), ...this.$refs.tree.getHalfCheckedKeys()]
      const idStr = keys.join(',')
      console.log(this.roleId)
      let { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idStr })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.getRoles()
      this.rightdialogVisible = false
    }
  }
}
</script>
<style lang="less" scoped>
.borderBottom {
  border-bottom: 1px solid #ccc;
  display: flex;
  align-items: center;
}
.borderTop {
  border-top: 1px solid #ccc;
  display: flex;
  align-items: center;
}
.borderTopc {
  border-top: none;
}
.el-tag {
  margin: 7px;
}
</style>
